@charset "utf-8";
body *{
    margin: 0px; 
    padding:0px;
}
/* product_list서식 */
ol,ul,li{
    list-style: none;
}
h3{
    text-align: center;
    font-size: 28px;
    margin-bottom: 20px;
}
.product_list{
    width:1050px;
    display: flex;
    justify-content: space-evenly;
    margin: 0px auto;
}
.product_list >li{
    width:249px;height:434px;
    /* border:1px solid #f00; */
}
h3{
    margin: 50px 0px 30px 0px;
    font-size: 28px;
    font-weight: 500;
}
.product_list>li img{
    height:320px;
}
.product_list> li>p:last-of-type{
    font-weight: bold;
}
.icon{
    border: 1px solid #ff5a5a;
    border-radius: 50%;
    background-color: #FF5A5A;
    color:#fff;
    display: block;
    width:30px;
    height: 30px;
    text-align: center;
    font-size: 20px;
    line-height: 28px;
    transform: translate(211px,-85px);
}
.icon:hover{
    background-color: #fff;
    color: #FF5A5A;
}
.banner{
    width: 100%;
}

.pr{
    color:#ff0000;
}
.product{
    overflow: hidden;
}
.props{
    transition: 0.3s;
}
.props:hover{
    transform: scale(1.1);
}