@charset "utf-8";

.log_main .inner{
  padding: 70px 0px 50px 0px;
  /* height: 400px; */
  text-align: center;
}
.log_main h2{
  text-align: center;
  font-size: 20px;
  margin-bottom: 30px;
}
.log_main label{
  display: none;
}
.log_main input[type=text],.log_main input[type=password]{
  width: 340px;
  height: 54px;
  box-sizing: border-box;
  padding: 0px 10px;
  margin: 5px 0px;
  border: 1px solid #ccc;
  outline: none;
  border-radius: 5px;
}
.btn{
  width:340px;
  border-radius: 5px;
  height: 54px;
  border: 1px solid #5F0080;
  margin: 5px 0px;
  cursor: pointer;
  font-size: 16px;
  
}
.c1{
  background-color: #5F0080;
  color:#fff;
  margin-top: 10px;
}
.c2{
  color:#5F0080;
  background-color: #fff;
}
.txt02{
  text-align: right;
  width: 340px;
  margin: 10px auto;
  cursor: pointer;
}