@charset "utf-8";
body{
  font-family: '맑은 고딕',arial,sans-serif;
  font-size: 12px;
  color:#333;
}
a{
  text-decoration: none;
  color:#333;
}
.inner{
  width:1050px;
  margin: 0px auto;
  position:relative;
}
header{
  box-shadow: 0px 0px 3px #666;
}
header .inner{
  height:156px;
}
.h_top{
  position:absolute;
  right: 0px;
  top:0px;
  padding-top:10px;
}
.h_top ul{
  display:flex;
}
.h_top ul li{
  margin-left:7px;
  cursor: pointer;
}
.h_top ul li::after{
  content:"|";
  color:#ccc;
  padding-left: 5px;
}
.h_top ul li:first-child{
  color:#5F0080;
}
.h_top ul li:last-child img{
  width:8px;
  padding:0px 0px 1px 5px;
}
.h_top ul li:last-child::after{
  display: none;
}
.logo {
  position: absolute;
  top:50%;
  transform: translateY(-50%);
}
.txt{
  position: absolute;
  top:50%;
  left:100px;
  transform: translateY(-50%);
  display: flex;
  font-size: 18px;
}
.txt p{
  font-weight: bold;
  cursor: pointer;
}
.txt p:first-of-type{
  color:#5F0080;
}
.txt p:first-of-type::after{
  content: "|";
  color:#ccc;
  padding: 0px 10px;
}
.txt p:last-of-type:hover{
  color:#5F0080;
}
.m_search{
  width:400px;
  height: 48px;
  border: 1px solid #5F0080;
  border-radius: 5px;
  outline: none;
  box-sizing: border-box;
  padding:0px 100px 0px 10px;
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.search{
  position:absolute;
  top:50%;
  transform: translateY(-50%);
  right: 330px;
}
.lnb{
  position: absolute;
  top:50%;
  transform: translateY(-50%);
  right: 0px;
  display: flex;
}
.lnb li{
  margin: 0px 10px;
  cursor: pointer;
}
.h_bottom{
  position: absolute;
  width: 1050px;
  bottom:0px;
  padding-bottom: 10px;
}
.h_bottom .category{
  float: left;
  font-size: 16px;
}
.h_bottom .category img{
  margin-right: 10px;
}
.h_bottom nav{
  width: 600px;
  margin: 0px auto;
}
.h_bottom .gnb{
  display:flex;
  font-size:16px;
}
.h_bottom .gnb li{
  width: 150px;
  text-align: center;
}
.h_bottom .gnb a:hover{
  text-decoration: underline;
  color:#5F0080;
}
.h_bottom .gnb span:hover{
  text-decoration: underline;
  color:#5F0080;
}
.h_bottom .guide{
  position:absolute;
  bottom: 5px;
  right: 0px;
  border: 1px solid #ccc;
  border-radius: 50px;
  padding:8px 10px;
  cursor: pointer;
}
.co1{
  color:#5F0080;
}
footer{
  border-top: 1px solid #ccc;
}
footer .inner{
  padding-top:30px ;
  height: 300px;
}
.txt1{
  position: absolute;
  font-size: 20px;
  line-height: 40px;
}
.p1{
  font-size: 28px;
}
.p2{
  font-size: 16px;
}
.f_nav{
  position: absolute;
  right: 0px;
}
.f_nav ul{
  display: flex;
  font-size: 14px;
}
.f_nav ul li{
  margin: 10px;
  cursor: pointer;
}
.txt2{
  position: absolute;
  top:88px;
  right: 104px;
  line-height: 20px;
}
.Not_inner{
  width:1050px;
  margin:0px auto;
  text-align: center;
  padding: 20px 0px;
  line-height: 60px;
  font-size: 28px;
}
.error{
  color:#f00;
  font-size: 32px;
}
.er_btn{
  width: 150px;
  height: 50px;
  border: none;
  background-color: #5F0080;
  color:#fff;
  cursor: pointer;
}